<template>
  <div class="card" style="height: 95%;">
    <div class="card__title">
      <b-row>
        <b-col style="max-width:fit-content !important;">
          <span>Driver Management</span>
        </b-col>
        <b-col style="text-align: right;">
          <span>
            <ABSButton
              :text="'View All'"
              classButton="button button--new"
              classIcon="icon-style-1"
              :disabled="false"
              @click="doViewAllDriverManagement"
            />
          </span>
        </b-col>
      </b-row>
    </div>
    <div class="card__body">
        <ACCFormList
            :prop="propList"
            @rowClicked="rowClicked"
            ref="ref_OPDriverManagement"
        >
            <template slot="sim_expire" slot-scope="data">
              <span v-if="data.item.sim_expiry_status == 'Y'" style="color: red">{{data.item.sim_expire}}</span>
              <span v-else>{{data.item.sim_expire}}</span>
            </template>
            <template slot="skck_expire" slot-scope="data">
              <span v-if="data.item.skck_expiry_status == 'Y'" style="color: red">{{data.item.skck_expire}}</span>
              <span v-else>{{data.item.skck_expire}}</span>
            </template>
        </ACCFormList>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
        propList: {
            OptionUrl: "/SP/SP_Dashboard",
            initialWhere: "",
            LineNo: 2,
            PageLevel: 1,
            TabIndex: 1,
            OrderBy: "employee_id",
            SourceField: "",
            ParamView: "",
            PerPage: 8,
        }
    };
  },
  methods: {
    rowClicked(record, index) {
      var param = record;
      this.$store.commit("setParamPage", param);
      this.$router.push({ name: "OP_DriverManagementView" });
    },
    doViewAllDriverManagement() {
      this.$router.push({ name: "OP_DriverManagement" });
    }
  },
  mounted() {
    this.$refs.ref_OPDriverManagement.doGetList("");
  },
};
</script>